/* z-index */
.mint-bread {
  background: #fff;
  white-space: nowrap;
  font-size: 0;
  width: 100%;
  overflow: auto;
}
.mint-bread > span {
  line-height: 42px;
  display: inline-block;
  position: relative;
  font-size: 16px;
  padding: 0 16px;
  color: #2F343B
}
.mint-bread > span.active {
  color: #06c1ae;
  border-bottom: 2px solid #06c1ae;
}
.mint-bread > span:after {
  content: '';
  display: block;
  position: absolute;
  width: 0;
  height: 0;
  right: -2px;
  top: 17px;
  border-style: solid;
  border-width: 4px;
  border-color: transparent transparent transparent #2F343B;
}
.mint-bread > span:last-child:after {
  display: none;
}



