
.mint-button {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border-radius: 5px;
  border: 0;
  box-sizing: border-box;
  display: block;
  font-size: 18px;
  height: 44px;
  outline: 0;
  overflow: hidden;
  position: relative;
  text-align: center;
}
.mint-button.is-disabled {
  opacity: .3;
}
.mint-button.is-plain:not(.is-disabled):active:after {
  content: " ";
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  position: absolute;
  opacity: 0.1;
}
.mint-button.is-plain {
  border-width: 1px;
  border-style: solid;
  box-shadow: none;
}
.mint-button-icon {
  vertical-align: middle;
  display: inline-block;
}
.mint-button--large {
  display: block;
  width: 100%;
}
.mint-button--normal {
  display: inline-block;
  font-size: 12px;
  padding: 0px 10px;
  height: 28px;
  border-radius: 14px;
}
.mint-button--small {
  display: inline-block;
  font-size: 11px;
  padding: 0px 8px;
  height: 25px;
  border-radius: 12px;
}
.small, .normal {
  margin-right:5px;
}
.large {
  margin-right:10px;
}



